import { blockCreator, blockGroup } from "../utils";
import Form from "./Form";

const RenderInput = function ({ type = "square" }) {
  const typeInputMap = new Map([
    [
      "square",
      ({ height = 100, width = 100 }) => (
        <>
          <div>
            <label htmlFor="height">:</label>
            <input
              id="height"
              type="number"
              name="height"
              defaultValue={parseInt(height)}
              placeholder=""
            />
          </div>
          <div>
            <label htmlFor="">:</label>
            <input
              type="number"
              id="width"
              name="width"
              defaultValue={parseInt(width)}
              placeholder=""
            />
          </div>
        </>
      ),
    ],
    [
      "circle",
      ({ radius = 50 }) => (
        <div>
          <label htmlFor="radius">:</label>
          <input
            type="number"
            id="radius"
            name="radius"
            defaultValue={parseInt(radius)}
          />
        </div>
      ),
    ],
  ]);
  const Inputs = typeInputMap.get(type);
  const block = arguments[0] || {};
  return <Inputs {...block.size} />;
};

const SetCanvasAccordingToWidthHeight = function ({
  height = 350,
  width = 450,
  style = {},
  onUpdateCanvas = function () {},
}) {
  const _readImage = function (file, imageWidth) {
    return new Promise((resolve, reject) => {
      if (!file.name) {
        return reject();
      }
      const reader = new FileReader();
      reader.onload = function () {
        const image = new Image();
        image.onload = function () {
          const originalWidth = image.width;
          image.width = imageWidth;
          image.height = (imageWidth * image.height) / originalWidth;
          resolve(image);
        };
        image.src = this.result;
      };
      reader.readAsDataURL(file);
    });
  };
  const _handleSubmit = async function (event) {
    try {
      const image = await _readImage(
        event.data.file,
        parseInt(event.data.imageWidth)
      );
      onUpdateCanvas({ ...event.data, image });
    } catch (e) {
      onUpdateCanvas({ ...event.data, file: null });
    }
  };
  return (
    <Form onSubmit={_handleSubmit} style={style}>
      <h3>:</h3>
      <div>
        <label htmlFor="">:</label>
        <input
          type="number"
          id="width"
          name="width"
          defaultValue={parseInt(width)}
          placeholder=""
        />
      </div>
      <div>
        <label htmlFor="">:</label>
        <input
          type="number"
          id="height"
          name="height"
          defaultValue={parseInt(height)}
          placeholder=""
        />
      </div>

      <div>
        <label htmlFor="image">:</label>
        <input type="file" accept="image/*" name="file" />
      </div>
      <div>
        <label htmlFor="imageWidth">:</label>
        <input
          type="number"
          name="imageWidth"
          id="imageWidth"
          defaultValue={parseInt(width)}
        />
      </div>
      <div>
        <button type="submit"></button>
        <button type="reset"></button>
      </div>
    </Form>
  );
};

const Settings = function ({
  blocks = [],
  canvasAttr = {},
  onUpdateBlock = function () {},
  onUpdateCanvas = function () {},
}) {
  const [activeGroup] = blockGroup(blocks, true);
  let currentBlock = {};
  if (!activeGroup.length) {
    return (
      <SetCanvasAccordingToWidthHeight
        {...canvasAttr}
        onUpdateCanvas={onUpdateCanvas}
      />
    );
  } else if (activeGroup.length === 1) {
    currentBlock = activeGroup[0];
  } else {
    return "";
  }

  const _handleSubmit = function (event) {
    let {
      color = "",
      width = 0,
      height = 0,
      left = 0,
      top = 0,
      radius = 0,
      type = "",
    } = event.data;
    if (type === "circle") {
      radius =
        radius ||
        Math.min(currentBlock.size.width, currentBlock.size.height) / 2;
      width = radius * 2;
      height = radius * 2;
    } else if (type === "square") {
      width = width || radius * 2;
      height = height || radius * 2;
      radius = 0;
    }
    const [newBlock] = blockCreator(
      {
        type,
        color,
        width: parseInt(width),
        height: parseInt(height),
        radius: parseInt(radius),
        left: parseInt(left),
        top: parseInt(top),
        id: currentBlock.id,
      },
      { canvas: canvasAttr }
    );
    onUpdateBlock(currentBlock.id, newBlock);
  };

  return (
    <Form onSubmit={_handleSubmit}>
      <h3>:</h3>
      <RenderInput {...currentBlock} />
      <div>
        <label htmlFor="type">:</label>
        <select
          name="type"
          id="type"
          key={currentBlock.type}
          defaultValue={currentBlock.type}
        >
          <option value="circle">circle</option>
          <option value="square">square</option>
        </select>
      </div>
      <div>
        <label htmlFor="color">:</label>
        <input
          key={currentBlock.size.color}
          type="color"
          defaultValue={currentBlock.size.color}
          name="color"
          id="color"
        />
      </div>
      <p>------</p>
      <div>
        <label htmlFor="left">X:</label>
        <input
          key={currentBlock.style.left}
          type="number"
          defaultValue={parseInt(currentBlock.style.left)}
          name="left"
          id="left"
        />
      </div>
      <div>
        <label htmlFor="top">Y:</label>
        <input
          key={currentBlock.style.top}
          type="number"
          defaultValue={parseInt(currentBlock.style.top)}
          name="top"
          id="top"
        />
      </div>
      <div>
        <button type="submit"></button>
        <button type="reset"></button>
      </div>
    </Form>
  );
};

export default Settings;
